
button {
  background-color: #fff;
  color: #006505;
  border: none;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  padding: 0.5em;
  font-size: 1.3em;
  border-radius: 5px;
  box-shadow: 0 0 2px #888;
  transition: all 0.3s ease;
}
button:hover, button:focus {
  background-color: #006505;
  color: #fff;
}
.link {
  font-size: 1.3em;
}
.content-title {
  color: #888;
  font-weight: 300;
  font-size: 2em;
  margin-bottom: 1em;
}
.navbar-nav {
  height: 80px;
}
li {
  height: 80px;
  padding-top: 15px;
}
.card-title {
  color: #555;
  font-size: 2em;
  font-weight: 300;
}
.card-content {
  padding: 1em;
}
td {
  padding: 0.5em;
}
tr.header,
td.header {
  font-weight: 700;
  background-color: #eee;
}
hr {
  background-color: #ddd;
  height: 1px;
  border: 0;
}



